import { Component, OnInit } from '@angular/core';
declare var $: any;  // gọi jquery vào component
import { NgxSpinnerService } from 'ngx-spinner';
import {HttpClient} from '@angular/common/http'
import {RootObject} from 'src/app/models/photo.model'
import { from } from 'rxjs';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  width :number
  widthBrowser  = window.innerWidth
  photos = [] as RootObject[]
  constructor(private spinner: NgxSpinnerService, private http: HttpClient) { }
  ngOnInit() {
    this.spinner.show();
    this.http.get('http://jsonplaceholder.typicode.com/posts').subscribe(
      res =>{
        this.photos = res as RootObject[]
        this.spinner.hide();
      }
    )
    document.getElementById("header").style.display = 'none'
    // console.log(document.getElementById("demo").offsetWidth)  // lấy chiều rộng của div
    this.width= document.getElementById("demo").offsetWidth
    document.getElementById("demo2").style.width = this.width+ 'px';
    $(window).resize(function() {   // hàm bắt theo độ rộng màn hình  
      if(window.innerWidth > 765){
        document.getElementById("demo2").style.display='block'
        this.width= document.getElementById("demo").offsetWidth
        document.getElementById("demo2").style.width = this.width+ 'px';
      } else{
        document.getElementById("demo2").style.display='none'
      }
    });
    $(window).scroll(function(){   // sự kiện bắt theo scroll 
      // console.log(pageYOffset);  // chỉ số scroll 
      if(pageYOffset <=  250){
        $('#header').slideUp(1000);
      }else{
        $("#header").slideDown(1000);
        document.getElementById("header").style.display = 'block'
      } 
    })
  }
}
